.category {
	&-page {
		display: flex;
		flex-direction: column;

		&-content {
			background-color: #fff;
			border-radius: 0 0 8px 8px;
			position: relative;
			z-index: 4;
			box-shadow: 0 20px 40px 0 rgba(0, 0, 0, 0.35);
		}

		&-filter {
			display: none;
			background-color: transparent;
			border: none;
			cursor: pointer;
			font-weight: 400;
			font-size: 10px;
			letter-spacing: -0.03em;
			font-family: 'Manrope';
			padding-left: 15px;
			position: relative;

			img {
				position: absolute;
				left: 0;
				top: 50%;
				transform: translate(0, -50%);
			}
		}

		&-higlight {
			margin-bottom: 50px;

			.category-card {
				max-width: 100%;

				&-content {
					max-width: 100%;
				}

				&-title {
					font-size: 52px;
				}
			}
		}

		&-block {
			margin: 0 10px;
			background-color: #f3f4f6;
			border-radius: 8px;
			padding: 30px;
			margin-bottom: 50px;
		}

		&-control {
			text-align: right;
			margin-bottom: 20px;
			line-height: 0;
		}

		&-btns {
			button {
				background-color: transparent;
				border: none;
				padding: 0;
				margin-right: 16px;
				cursor: pointer;
				width: 24px;
				height: 24px;
				line-height: 0;

				&:last-child {
					margin-right: 0;
				}

				&.active {
					filter: brightness(0%);
				}
			}
		}

		&-items {
			.popular-card {
				margin-bottom: 8px;

				&-title {
					font-size: 32px;
					margin-bottom: 10px;
				}

				&-content {
					min-height: 380px;
					border: none;
					margin-bottom: 0;
				}

				&-subtitle {
					font-size: 14px;
				}

				&-btns {
					.ui-btn {
						height: 42px;
						line-height: 42px;
						font-size: 14px;
					}
				}

				&-links {
					max-width: 150px;
					flex: 0 0 150px;

					.ui-btn {
						height: 42px;
						line-height: 42px;
						margin-bottom: 8px;
						margin-right: 0;
						font-size: 14px;
						width: 100%;

						&:last-child {
							margin-bottom: 0;
						}
					}
				}

				&-table {
					max-width: calc(100% - 178px);
					flex: 0 0 calc(100% - 178px);
					display: none;
					margin-right: 28px;
					padding-bottom: 1px;
				}

				table {
					outline: 1px solid #ccd2d6;
					border-radius: 4px;
					font-family: 'Manrope';
					font-weight: 500;
					font-size: 14px;
					border-collapse: collapse;
					font-weight: 500;
					width: 100%;

					.border-none {
						border: none;
					}
				}

				table th {
					text-align: left;
				}

				thead th {
					color: #878b95;
					padding: 8px 20px;
					border-bottom: 1px solid #ccd2d6;
					border-right: 1px solid #ccd2d6;
					font-weight: 500;
					max-width: 33.333%;
					width: 100%;

					&:last-child {
						border-right: none;
					}
				}

				tbody td {
					padding: 8px 20px;
					border-bottom: 1px solid #ccd2d6;
					border-right: 1px solid #ccd2d6;

					&:last-child {
						border-right: none;
					}
				}

				tbody tr:last-child td {
					border-bottom: none;
				}

				&.active {
					max-width: none;

					.popular-card-table {
						display: block;
					}

					.popular-card {
						&-content {
							display: flex;
							background-color: transparent;
							max-width: none;
							min-height: auto;
							padding: 0;
							// flex-direction: row-reverse
						}

						&-head {
							order: 2;
							max-width: calc(100% - 450px);
							flex: 0 0 calc(100% - 450px);
						}

						&-title {
							margin-bottom: 20px;
						}

						&-subtitle {
							display: none;
						}

						&-img {
							position: static;
							max-width: 450px;
							border-radius: 8px;
							overflow: hidden;
							order: 1;
							margin-right: 30px;

							img {
								object-position: center;
							}
						}

						&-btns {
							display: none;
						}

						&-links {
							display: flex;
							flex-direction: column;
						}

						&-info {
							display: flex;
						}
					}
				}
			}

			.col-ss-12 {
				&:last-child {
					.popular-card.active {
						margin-bottom: 8px;
					}
				}
			}

			.ui-interpreter {
				height: 24px;
				line-height: 22px;
				font-size: 14px;
				padding: 0 8px;
			}
		}

		&-body {
			margin-bottom: 22px;
		}

		.advantages {
			&-body {
				&-item {
					max-width: 421px;
				}

				&-list {
					max-width: 1266px;
					margin: 0 auto;
				}
			}
		}

		&-info {
			.row {
				margin-left: -15px;
				margin-right: -15px;
			}

			[class*='col-'] {
				padding-left: 15px;
				padding-right: 15px;
			}
		}
	}

	&-info {
		margin-bottom: 50px;

		&-title {
			font-weight: 500;
			font-size: 24px;
			line-height: 1.3;
			text-transform: uppercase;
			font-family: 'Oswald';
			margin-bottom: 30px;
		}

		&-subtitle {
			font-weight: 500;
			font-size: 18px;
			line-height: 1.3;

			p {
				&:last-child {
					margin-bottom: 0;
				}
			}

			ul {
				padding-left: 28px;

				&:last-child {
					margin-bottom: 0;
				}
			}

			li {
				list-style: disc;
			}
		}
	}

	&-filter {
		position: relative;
		margin-bottom: 8px;

		&-block {
			background-color: #fff;
			padding: 20px;
			border-radius: 8px;
			margin-bottom: 2px;

			&:last-child {
				margin-bottom: 0;
			}
		}

		&-btns {
			display: flex;

			.ui-btn {
				width: 50%;
				margin-right: 8px;
				height: 42px;
				line-height: 42px;
				font-size: 14px;
				padding: 0;

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.close-filter {
			display: none;
			position: absolute;
			right: -16px;
			top: -16px;
			width: 32px;
			height: 32px;
			background-color: #121317;
			border-radius: 50%;
			border: none;
			line-height: 0;

			img {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
			}
		}

		&-list {
			display: none;

			a {
				display: block;
				font-weight: 600;
				font-size: 16px;
				line-height: 1.1;
				letter-spacing: 0.01em;
				text-transform: uppercase;
				font-family: 'Oswald';
				margin-bottom: 16px;
				transition: color 0.4s ease;

				&:hover {
					color: #910828;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}

			.ui-checkbox {
				margin-bottom: 8px;

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		&-title {
			font-family: 'Oswald';
			font-weight: 500;
			font-size: 24px;
			line-height: 1.3;
			text-transform: uppercase;
			position: relative;
			cursor: pointer;
			width: 100%;
			border: none;
			background-color: transparent;
			text-align: left;
			padding: 0;

			&.fs-16 {
				font-size: 16px;
			}
		}

		&-caption {
			font-weight: 500;
			font-size: 24px;
			line-height: 1.3;
			text-transform: uppercase;
			margin-bottom: 20px;
			font-family: 'Oswald';
			padding-bottom: 20px;
			border-bottom: 1px solid #ccd2d6;
			padding-left: 10px;
			padding-right: 10px;
		}

		&-type {
			border-bottom: 1px solid #ccd2d6;
			padding-bottom: 10px;
			margin-bottom: 10px;
			padding-left: 10px;
			padding-right: 10px;

			&.parameters {
				border: none;
				padding-bottom: 0 !important;
				margin-bottom: 15px;
			}

			&.active {
				padding-bottom: 24px;

				.category-filter-list {
					display: block;
				}

				.category-filter-title {
					margin-bottom: 24px;
				}

				.category-filter-icon {
					transform: translate(0, -50%) rotate(45deg);
				}
			}

			&:last-child {
				margin-bottom: 0;
			}

			&:first-child {
				border-top: 1px solid #ccd2d6;
				padding-top: 10px;
			}
		}

		&-icon {
			position: absolute;
			right: 0;
			top: 50%;
			transform: translate(0, -50%);
			width: 18px;
			height: 18px;
			border: 1px solid #ccd2d6;
			border-radius: 50%;
			pointer-events: none;

			img {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}

	@media (max-width: 1520.99px) {
		&-page {
			&-higlight {
				margin-bottom: 40px;

				.category-card {
					&-title {
						font-size: 42px;
					}

					&-content {
						min-height: 400px;
					}
				}
			}

			&-items {
				.popular-card {
					&-title {
						font-size: 24px;
					}

					&-btns {
						padding: 0 20px;
					}

					&-table {
						margin-right: 8px;
						max-width: calc(100% - 158px);
						flex: 0 0 calc(100% - 158px);
					}

					thead th {
						font-size: 10px;
						padding: 4px 10px;
					}

					tbody td {
						padding: 8px 10px;
					}

					&.active {
						.popular-card-img {
							max-width: 290px;
							flex: 0 0 290px;
							margin-right: 8px;
							height: 225px;

							img {
								object-fit: cover;
							}
						}

						.popular-card-head {
							max-width: calc(100% - 298px);
							flex: 0 0 calc(100% - 298px);
						}
					}
				}
			}
		}

		&-filter {
			&-list {
				a {
					font-size: 14px;
				}
			}

			&-title {
				font-size: 20px;
			}

			&-caption {
				font-size: 20px;
				padding-left: 0;
				padding-right: 0;
			}

			&-type {
				padding-left: 0;
				padding-right: 0;
			}
		}

		&-info {
			&-title {
				margin-bottom: 20px;
			}

			p {
				font-size: 14px;
			}

			li {
				font-size: 14px;
			}
		}
	}

	@media (max-width: 1024.99px) {
		&-page {
			&-block {
				padding-left: 20px;
				padding-right: 20px;
				margin-left: 0;
				margin-right: 0;
				margin-bottom: 30px;
			}

			&-filter {
				display: block;
			}

			&-btns {
				button {
					width: 17px;
					height: 17px;
					margin-right: 10px;
				}
			}

			&-control {
				display: flex;
				align-items: center;
				justify-content: space-between;
			}

			&-higlight {
				margin-bottom: 20px;
				padding: 0 20px;

				.category-card {
					&-title {
						font-size: 32px;
					}

					&-content {
						min-height: 280px;
						padding: 20px;
					}
				}
			}

			&-items {
				.popular-card {
					max-width: none;

					&-content {
						max-width: none;
						margin-bottom: 8px;
					}

					&-btns {
						padding: 0;
					}

					&.active {
						margin-bottom: 25px;

						.popular-card-content {
							overflow: inherit;
						}

						.popular-card-title {
							margin-bottom: 8px;
						}

						tbody td {
							padding-top: 5px;
							padding-bottom: 5px;
						}

						// thead th {
						//   padding-top: 4px;
						//   padding-bottom: 4px;
						// }

						.popular-card-img {
							max-width: 320px;
							flex: 0 0 320px;
							height: 250px;
						}

						.popular-card-head {
							max-width: calc(100% - 328px);
							flex: 0 0 calc(100% - 328px);
						}
						.popular-card-table {
							max-width: none;
							flex: none;
							width: 100%;
							margin-right: 0;
							margin-bottom: 8px;
							padding-right: 1px;
						}

						.popular-card-info {
							flex-direction: column;
						}

						.popular-card-links {
							flex-direction: row;
							max-width: none;
							flex: none;

							.ui-btn {
								margin-bottom: 0;
								margin-right: 8px;

								&:last-child {
									margin-right: 0;
								}
							}
						}
					}
				}
			}

			&-body {
				margin-bottom: 12px;
			}

			&-info {
				padding: 0 20px;
			}

			.advantages-content {
				margin-bottom: -120px;
				top: -120px;
			}
		}

		&-info {
			margin-bottom: 30px;
		}

		&-filter {
			max-width: 300px;
			width: 100%;
			position: absolute;
			left: 0;
			top: 30px;

			&-wrapper {
				position: fixed;
				left: 0;
				top: 101px;
				background-color: rgba(0, 0, 0, 0.2);
				width: 100%;
				height: 100vh;
				z-index: 1300;
			}

			.close-filter {
				display: block;
			}

			&-wrapper {
				display: none;

				&.active {
					display: block;
				}
			}
		}
	}

	@media (max-width: 745.99px) {
		&-page {
			&-block {
				padding-left: 10px;
				padding-right: 10px;
				padding-top: 20px;
				padding-bottom: 20px;
				margin-bottom: 20px;
			}

			&-higlight {
				padding: 0 10px;
				margin-bottom: 10px;

				.category-card {
					&-title {
						font-size: 24px;
					}

					&-content {
						min-height: 220px;
					}
				}
			}

			&-items {
				.popular-card {
					margin-bottom: 20px;

					&-btns {
						flex-direction: row;

						.ui-btn {
							margin-bottom: 0;
						}
					}

					&-content {
						flex-direction: column;
					}

					&-links {
						.ui-btn {
							border-radius: 2px;
						}
					}

					&.active {
						margin-bottom: 20px;

						tbody td {
							font-size: 10px;
							padding-bottom: 4px;
							padding-top: 4px;
						}

						.popular-card-img {
							max-width: none;
							flex: none;
							height: 275px;
							margin-bottom: 8px;
						}

						.popular-card-head {
							max-width: none;
							flex: none;
						}
					}
				}

				.col-ss-12:last-child {
					.popular-card.active {
						margin-bottom: 20px;
					}
				}
			}

			&-body {
				margin-bottom: 0;
			}

			&-info {
				padding: 0 10px;
			}

			.advantages-content {
				margin-bottom: -70px;
				top: -70px;
			}
		}

		&-filter {
			&-wrapper {
				top: 74px;
			}
		}
	}
}
